.profileComponents {
  text-align: center;
}

.create-profile {
  margin-top: -80px;
}

.profileTitle {
  display: flex;
  justify-content: center;
  background-color: #b45a05;
  color: #ffffff;
  font-size: 24px;
  padding: 10px;
  margin-bottom: 20px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  cursor: default;
}

.profileSubtitle {
  text-align: center;
  background-color: #ffbbb0;
  color: #753901;
  font-size: 16px;
  padding: 5px;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 20px;
  cursor: default;
}

.profileInput {
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  text-indent: 10px;
  border-radius: 5px;
  background-color: #ffffff;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 16px;
  color: #000000;
  border: none;
  outline: 0;
}

.profileInputWithoutIndent {
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 5px;
  background-color: #ffffff;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 16px;
  color: #000000;
  border: none;
  outline: 0;
}

.profileGender {
  margin-top: 15px;
  margin-bottom: 15px;
  align-items: center;
}

input[type='radio'] {
  display: inline-block;
  width: 1em;
  height: 1em;
  border-radius: 10px;
  background-color: #ffffff;
  border: #b45a05 3px solid;
}

input[type='radio']:checked {
  display: inline-block;
  width: 1em;
  height: 1em;
  border-radius: 10px;
  background-color: #b45a05;
  border: #b45a05 3px solid;
}

.profileIntroduce {
  border: none;
  font-size: 12px;
  padding: 5px;
  outline: none;
  width: 100%;
  height: 300px;
  resize: none;
}

.profileIntroduceCount {
  color: #808080;
  font-size: 10px;
  justify-content: right;
  text-align: right;
}

.profileLabel span {
  /*사진 변경 버튼*/
  margin-right: 10px;
  font-size: 16px;
  cursor: pointer;
}

.profileAttachment {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profileAttachment img {
  height: 120px;
  width: 120px;
  border-radius: 50%;
}

.profileButton {
  text-align: center;
  background-color: #b45a05;
  border-radius: 10px;
  padding: 10px;
  color: white;
  margin-top: 10px;
  margin-bottom: 10px;
  cursor: pointer;
  font-size: 20px;
}

.myPageButtons {
  display: flex;
  flex-direction: column;
}

.myPageButtonsChild {
  color: #b45a05;
  background-color: #ffbbb0;
  margin: 5px;
  align-items: center;
  font-size: 16px;
  border-radius: 10px;
  padding: 10px;
  text-align: center;
  cursor: pointer;
  border: none;
}

.deleteConfirmBox {
  max-width: 300px;
  text-align: center;
  background-color: #ffbbb0;
  color: #ffffff;
  border-radius: 10px;
  padding: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
  line-height: 150%;
}

.deleteAccountButtons {
  display: flex;
}

.deleteNoButton {
  border: none;
  flex: 9;
  margin-top: 10px;
  margin-left: 2px;
  margin-right: 2px;
  padding: 5px;
  background-color: #b45a05;
  color: white;
  border-radius: 30px;
}

.deleteYesButton {
  border: none;
  flex: 1;
  margin-top: 10px;
  margin-left: 2px;
  margin-right: 2px;
  padding: 5px;
  background-color: #ff0000;
  color: white;
  border-radius: 30px;
}
